import { createClient, SupabaseClient } from '@supabase/supabase-js';

// Types are now centralized in /src/lib/types/database.ts
// Import from there: import { Database, UserProfile, etc. } from '@/lib/types/database'
import type { Database } from '@/lib/types/database';

// Environment variables - strict validation required
const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;
const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY;

// Validation stricte des variables d'environnement
if (!supabaseUrl || supabaseUrl === 'https://your-project.supabase.co') {
  throw new Error('âŒ VITE_SUPABASE_URL est requise et doit Ãªtre configurÃ©e dans le fichier .env');
}

if (!supabaseAnonKey || supabaseAnonKey === 'your-anon-key') {
  throw new Error('âŒ VITE_SUPABASE_ANON_KEY est requise et doit Ãªtre configurÃ©e dans le fichier .env');
}

// Create Supabase client with validated environment variables
export const supabase: SupabaseClient = createClient(
  supabaseUrl, 
  supabaseAnonKey, 
  {
  auth: {
    autoRefreshToken: true,
    persistSession: true,
    detectSessionInUrl: true,
    storage: typeof window !== 'undefined' ? window.localStorage : undefined,
  },
  realtime: {
    params: {
      eventsPerSecond: 2,
    },
  },
  global: {
    headers: {
      'x-my-custom-header': 'myfithero-app',
    },
  },
});

// Fonction utilitaire pour vÃ©rifier la configuration Supabase
export const validateSupabaseConfig = () => {
  const config = {
    url: supabaseUrl,
    hasAnonKey: !!supabaseAnonKey,
    isValidUrl: supabaseUrl?.startsWith('https://') && supabaseUrl.includes('.supabase.co'),
  };
  
  console.log('ðŸ“Š Configuration Supabase:', {
    url: config.url,
    hasAnonKey: config.hasAnonKey,
    isValidUrl: config.isValidUrl,
  });
  
  return config;
};

// Utility functions for common operations
export const supabaseHelpers = {
  // Get current user
  getCurrentUser: async () => {
    const { data: { user }, error } = await supabase.auth.getUser();
    if (error) {
      console.error('Error getting current user:', error);
      return null;
    }
    return user;
  },

  // Get user profile
  getUserProfile: async (userId: string) => {
    const { data, error }: any = await supabase
      .from('user_profiles')
      .select('*')
      .eq('id', userId)
      .single();
    
    if (error) {
      console.error('Error getting user profile:', error);
      return null;
    }
    return data;
  },

  // Update user profile
  updateUserProfile: async (userId: string, updates: Database['public']['Tables']['user_profiles']['Update']) => {
    const { data, error }: any = await supabase
      .from('user_profiles')
      .update({ ...updates, updated_at: new Date().toISOString() })
      .eq('id', userId)
      .select()
      .single();
    
    if (error) {
      console.error('Error updating user profile:', error);
      throw error;
    }
    return data;
  },

  // Generic data fetcher
  fetchData: async <T>(
    table: string,
    userId: string,
    dateFrom?: string,
    dateTo?: string,
    limit?: number
  ): Promise<T[]> => {
    let query = supabase
      .from(table)
      .select('*')
      .eq('user_id', userId);

    if (dateFrom) {
      query = query.gte('date', dateFrom);
    }
    if (dateTo) {
      query = query.lte('date', dateTo);
    }
    if (limit) {
      query = query.limit(limit);
    }

    query = query.order('date', { ascending: false });

    const { data, error }: any = await query;
    
    if (error) {
      console.error(`Error fetching data from ${table}:`, error);
      throw error;
    }
    
    return data as T[];
  },

  // Generic data inserter
  insertData: async <T>(
    table: string,
    data: any
  ): Promise<T> => {
    const { data: result, error } = await supabase
      .from(table)
      .insert(data)
      .select()
      .single();
    
    if (error) {
      console.error(`Error inserting data to ${table}:`, error);
      throw error;
    }
    
    return result as T;
  },

  // Generic data updater
  updateData: async <T>(
    table: string,
    id: string,
    updates: any
  ): Promise<T> => {
    const { data, error }: any = await supabase
      .from(table)
      .update({ ...updates, updated_at: new Date().toISOString() })
      .eq('id', id)
      .select()
      .single();
    
    if (error) {
      console.error(`Error updating data in ${table}:`, error);
      throw error;
    }
    
    return data as T;
  },

  // Delete data
  deleteData: async (table: string, id: string) => {
    const { error } = await supabase
      .from(table)
      .delete()
      .eq('id', id);
    
    if (error) {
      console.error(`Error deleting data from ${table}:`, error);
      throw error;
    }
  },

  // Subscribe to realtime changes
  subscribeToChanges: (
    table: string,
    userId: string,
    callback: (payload: any) => void
  ) => {
    return supabase
      .channel(`${table}_changes`)
      .on(
        'postgres_changes',
        {
          event: '*',
          schema: 'public',
          table: table,
          filter: `user_id=eq.${userId}`,
        },
        callback
      )
      .subscribe();
  },
};

// Auth helpers
export const authHelpers = {
  signUp: async (email: string, password: string, metadata?: any) => {
    const { data, error }: any = await supabase.auth.signUp({
      email,
      password,
      options: {
        data: metadata,
      },
    });
    
    if (error) {
      console.error('Error signing up:', error);
      throw error;
    }
    
    return data;
  },

  signIn: async (email: string, password: string) => {
    const { data, error }: any = await supabase.auth.signInWithPassword({
      email,
      password,
    });
    
    if (error) {
      console.error('Error signing in:', error);
      throw error;
    }
    
    return data;
  },

  signOut: async () => {
    const { error } = await supabase.auth.signOut();
    
    if (error) {
      console.error('Error signing out:', error);
      throw error;
    }
  },

  resetPassword: async (email: string) => {
    const { data, error }: any = await supabase.auth.resetPasswordForEmail(email);
    
    if (error) {
      console.error('Error resetting password:', error);
      throw error;
    }
    
    return data;
  },
};

// Utility functions for health checks and error handling
export const supabaseHealthCheck = {
  /**
   * Test basic connectivity to Supabase
   */
  testConnection: async (): Promise<{ success: boolean; error?: string }> => {
    try {
      const { data, error }: any = await supabase.from('user_profiles').select('count', { count: 'exact', head: true });
      
      if (error) {
        return { success: false, error: error.message };
      }
      
      return { success: true };
    } catch (error: any) {
      return { 
        success: false, 
        error: error instanceof Error ? error.message : 'Unknown connection error' 
      };
    }
  },

  /**
   * Test authentication service specifically
   */
  testAuthService: async (): Promise<{ success: boolean; error?: string }> => {
    try {
      const { data, error }: any = await supabase.auth.getSession();
      
      if (error) {
        return { success: false, error: error.message };
      }
      
      return { success: true };
    } catch (error: any) {
      return { 
        success: false, 
        error: error instanceof Error ? error.message : 'Auth service error' 
      };
    }
  }
};

// Network retry utility with exponential backoff
export const retryWithBackoff = async <T>(
  fn: () => Promise<T>,
  maxRetries: number = 3,
  baseDelay: number = 1000
): Promise<T> => {
  let lastError: Error;
  
  for (let attempt = 0; attempt <= maxRetries; attempt++) {
    try {
      return await fn();
    } catch (error: any) {
      lastError = error instanceof Error ? error : new Error('Unknown error');
      
      if (attempt === maxRetries) {
        throw lastError;
      }
      
      // Exponential backoff: 1s, 2s, 4s
      const delay = baseDelay * Math.pow(2, attempt);
      console.warn(`Attempt ${attempt + 1} failed, retrying in ${delay}ms:`, lastError.message);
      
      await new Promise(resolve => setTimeout(resolve, delay));
    }
  }
  
  throw lastError!;
};

export default supabase;